

<template>
  <v-card style="background-color: transparent" elevation="0" :disabled="loading">
    <v-row >
      <v-col md="12" cols="12">
        <BlockItem>

          <template slot="icon">mdi-google-nearby</template>
          <span  slot="title">درخواست های طراح</span>


          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.product.title`]="{item}">
              <a class="secondary--text" @click="detail(item.product.id)" v-if="item.product.title.length>50">{{item.product.title.substring(0,50) + '...'}}</a>
              <a class="secondary--text" @click="detail(item.product.id)" v-else>{{item.product.title}}</a>
            </template>

            <template v-slot:[`item.payed`]="{item}">
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item && item.product && item.product.product_type && item.product.product_type.value && item.payed && (item.product.product_type.value=='tender')" >پرداخت شده</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else-if="item && item.product && item.product.product_type && item.product.product_type.value && !item.payed && (item.product.product_type.value=='tender')">پرداخت نشده</span>
              <span class="table-flag-enabled table-flag-enabled--yellow" v-else >پس پرداخت</span>
            </template>

            <template v-slot:[`item.status`]="{item}">
              <span class="table-flag-enabled table-flag-enabled--yellow" v-if="item.chosen===null && item.confirmed===null" >بررسی نشده</span>
              <span class="table-flag-enabled table-flag-enabled--green" v-else-if="item.chosen===true || item.confirmed===true" >تایید شده</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else >رد شده</span>
            </template>

            <template v-slot:[`item.price`]="{item}">
              <span v-if="item && item.price" style="font-size: 12px" >{{item.price | digit}} تومان</span>
            </template>

            <template v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-center" v-if="item.chosen===null && item.confirmed===null">
                <a-sbtn @clickBtn="toEditPrice(item.id)" color="info" icon="mdi-currency-usd" alt="تغییر پیشنهاد" />
              </div>
            </template>
          </v-data-table>
        </BlockItem>
      </v-col>
    </v-row>

    <v-dialog
        v-model="priceDialog"
        max-width="320"
    >
      <v-card>
        <a-card-title>تغییر قیمت</a-card-title>
        <v-card-text class="py-6" >
          <v-form ref="price">
            <v-text-field
                v-if="renderComponent"
                outlined
                dense
                @keypress="isNumber($event)"
                hide-details
                :rules="[$store.state.rules.required(price,'قیمت')]"
                v-model="price"
                label="قیمت پیشنهادی"
                color="txtf"
            >
              <template v-slot:append>
                <img width="24" height="24" src="../../assets/icon-toman.svg"/>
              </template>

            </v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer/>
          <v-btn
              height="36"
              dark
              @click="changePrice()"
              color="#222e3c"
          >
            تغییر دادن
          </v-btn>
        </v-card-actions>
      </v-card>

    </v-dialog>

    <ProductSingleDialog :ddata="product" :public-use="true"/>

  </v-card>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
import numeral from "numeral";
import {isChars} from "@/components/Misc/isChars";
import {forceRerender} from "@/components/Misc/forceRerender";
import ProductSingleDialog from "@/components/Misc/ProductSingleDialog";

export default {
  name: "AllRequestsProductDesigners",
  mixins : [isChars,forceRerender],
  components : {
    ProductSingleDialog,
    ACardTitle,
    ASbtn,
    BlockItem
  },
  created() {
    this.fetchDesignerProducts()
  },

  data () {
    return {
      priceDialog : false,
      price : null,
      designerPrice : null,
      productSelected : null,
      data : {
        data : []
      },
      loading : false,
      product: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
    }
  },
  methods : {
    detail(id){
      this.product.dialog=true;
      this.product.idSelected = id;
    },
    toEditPrice(productId) {
      this.price=null;
      this.forceRerender();
      this.productSelected = productId;
      this.priceDialog = true;
    },
    changePrice() {
      this.$refs.price.validate();
      if(this.$refs.price.validate()){
        this.priceDialog = false;
        this.loading = true;
        axios.patch(this.$store.state.host + '/projects/api/v1/productdesigner/change-productdesigner/'+this.productSelected , {
          price : this.designerPrice
        },{
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.notify('قیمت بروز شد','info')
          this.fetchDesignerProducts();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    fetchDesignerProducts () {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/productdesigner/userproductdesigners',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.data = response.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading=false;
      })
    }
  },computed : {
    headers() {
      return [
        {
          text: 'عنوان پروژه',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'product.title',
        },
        {
          text: 'قیمت پیشنهادی', class: 'header-table-gray',
          align: 'center',
          value: 'price',
        },
        {
          text: 'وضعیت', class: 'header-table-gray',
          align: 'center',
          value: 'status',
        },
        {
          text: 'و.پرداخت', class: 'header-table-gray',
          align: 'center',
          value: 'payed',
        },{
          text: 'امتیاز', class: 'header-table-gray',
          align: 'center',
          value: 'point',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },
      ]
    },
  },
  watch : {
    'price'(val) {
      if (this.price) {
        this.price = numeral(val).format('0,0');
        this.designerPrice = numeral(this.price).value();
      } else {
        this.designerPrice = null;
      }
    },
  }
}
</script>

